body {
    background: linear-gradient(135deg, #ffdde1, #ee9ca7);
    font-family: 'Arial', sans-serif;
    margin: 0;
    overflow: hidden;
}

.home-container, .page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}

.heart {
    font-size: 80px;
    transition: transform 0.3s;
}

.heart:hover {
    transform: scale(1.3);
}

.glowing-text {
    font-size: 36px;
    font-weight: bold;
    color: white;
    text-shadow: 0 0 10px pink, 0 0 20px hotpink;
}

.petal {
    position: absolute;
    top: -10px;
    font-size: 20px;
    animation: fall linear infinite;
}

@keyframes fall {
    from {
        transform: translateY(0);
        opacity: 1;
    }
    to {
        transform: translateY(100vh);
        opacity: 0;
    }
}